.search-form {
  width: 60px;
  margin: 0 auto 40px 20px;
  display: flex;
}

.search-input {
  height: 40px;
  border: 1px solid #303030;
  border-radius: 8px;
  flex-grow: 1;
  padding-left: 15px;
  font-size: 18px;
}

.search-btn {
  color: #fff;
  text-transform: uppercase;
  background: #4fb53f;
  border: none;
  border-radius: 8px;
  align-items: center;
  margin-left: 10px;
  padding: 10px 20px;
  font-size: 14px;
  display: flex;
}

.search-btn, .load-more {
  color: #fff;
  text-transform: uppercase;
  background-color: #2ecc71;
  border: none;
  margin-left: 10px;
  padding: 10px 20px;
  font-size: 14px;
  transition: background-color .25s linear;
}

